<script setup lang="ts">
    import { useRoute } from "vue-router";
    import TheBannner from "@/components/layout/TheBannner.vue";
    import useFetch from "@/composables/useFetch";
    import { ref } from "vue";
    import { timeFormat, insertComma } from "@/util/common";
    import router from "@/router";
    import { useAlertStore } from "@/store/useAlert";

    const alert = useAlertStore();

    type CourseInfo = {
        no: string; // 순번
        cousNm: string; // 강좌명
        cousWkdNm: string; // 강좌 요일
        cousTime: string; // 강좌 시간
        clubFaclNm: string; // 클럽 시설명
        totalFixnuCunt: string; // 총 정원 수
        cousCage: string; // 강좌 연령
        cousMcnt: string; // 현재 수강 인원
    };
    const route = useRoute();
    const sprtClubNo = route.query.sprtClubNo;
    const goUrl = process.env.VUE_APP_WEB_URL + "/club/";

    const evfxCdOps = ref<EvfxResDto[]>([]);
    const chosenEvfxCd = ref<EvfxResDto | null>(null);

    const { data } = useFetch<ClubDetailResDto>({
        url: "/search/sports-detail",
        param: () => ({ sprtClubNo: sprtClubNo }),
        onSuccess: data => {
            evfxCdOps.value = [{ evfxNm: "전체", evfxCd: "" }, ...data.list];
            chosenEvfxCd.value = { evfxNm: "전체", evfxCd: "" };
        }
    });
    const { data: programDetail } = useFetch<CourseInfo[]>({
        url: "/search/sports-detail-program",
        dependancy: [() => chosenEvfxCd.value],
        param: () => ({ sprtClubNo: sprtClubNo, evfxCd: chosenEvfxCd.value?.evfxCd ?? "" }),
        onSuccess: data => {
            console.log(data);
        }
    });
</script>

<template>
    <TheBannner title="자세히 보기" :page-map="['스포츠클럽 목록', '자세히 보기']" />
    <div class="inner-w clubDetail">
        <div class="lineBorder"></div>

        <div class="clubDetail-top d-flex">
            <div class="thumb">
                <div class="thumb-imgWrap">
                    <img src="@/assets/imgs/main_visual_m01.jpg" alt="" />
                </div>
                <ul class="d-flex options">
                    <li class="d-flex f-column jc-center" :class="{ 'is-active': data?.wifiOffrYn === 'Y' }">
                        <VIcon icon="ic_wifi" :style="{ 'font-size': '20px' }" />무선인터넷
                    </li>
                    <li class="d-flex f-column jc-center" :class="{ 'is-active': data?.pkgPsbYn === 'Y' }">
                        <VIcon icon="ic_parking " :style="{ 'font-size': '20px' }" />주차
                    </li>
                    <li class="d-flex f-column jc-center" :class="{ 'is-active': data?.dsbpFaclOffrYn === 'Y' }">
                        <VIcon icon="ic_ppl" :style="{ 'font-size': '20px' }" />장애인시설
                    </li>
                    <li class="d-flex f-column jc-center" :class="{ 'is-active': data?.shtlOprtYn === 'Y' }">
                        <VIcon icon="ic_bus" :style="{ 'font-size': '20px' }" />셔틀버스
                    </li>
                </ul>
            </div>
            <div class="infos">
                <h3 class="d-flex ai-center">
                    {{ data?.clubNm
                    }}<a :href="goUrl + data?.rpsUrl" class="goBtn ml-10"
                        ><VIcon icon="ic_club_home" :style="{ color: '#fff', 'font-size': '12px' }"
                    /></a>
                </h3>
                <dl class="d-flex ai-center mb-11">
                    <dt class="d-flex ai-center">
                        <span class="mr-10"
                            ><VIcon icon="ic_location_green" :style="{ color: '#fff', 'font-size': '12px' }" /></span
                        >주소
                    </dt>
                    <dd>
                        {{ data?.clubPostAddr }}
                    </dd>
                </dl>
                <dl class="d-flex ai-center">
                    <dt class="d-flex ai-center">
                        <span class="mr-10"><VIcon icon="ic_phone_white" :style="{ color: '#fff' }" /></span>전화번호
                    </dt>
                    <dd>
                        {{ data?.rpsTelno }}
                    </dd>
                </dl>
                <div class="lineBorder mt-30 mb-30"></div>
                <div class="badgeList">
                    <span>운영종목</span>
                    <ul>
                        <li v-for="item in data?.list">{{ item.evfxNm }}</li>
                    </ul>
                </div>
                <div class="badgeList">
                    <span>운영시설</span>
                    <!-- <ul>
                        <li v-for="item in data?.list">{{ item.evfxNm }}</li>
                    </ul> -->
                </div>
            </div>
        </div>
        <ul class="clubDetail-jongmok d-flex">
            <li
                v-for="(e, index) in evfxCdOps"
                :key="index"
                :class="{ 'is-active': chosenEvfxCd?.evfxCd === e.evfxCd }"
            >
                {{ e.evfxNm }}
            </li>
        </ul>
        <span class="tableTit">프로그램 안내</span>
        <table class="program-table">
            <colgroup>
                <col style="width: 100px" />
                <col style="width: 100px" />
                <col style="width: 100px" />
                <col style="width: 100px" />
                <col style="width: 100px" />
                <col style="width: 100px" />
                <col style="width: 100px" />
            </colgroup>
            <thead>
                <tr>
                    <th class="tr-line">종목</th>
                    <th>프로그램명</th>
                    <th>요일</th>
                    <th>시간</th>
                    <th>대상</th>
                    <th>정원</th>
                    <th>금액</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(tr, index) in programDetail" :key="index">
                    <td></td>
                    <td>{{ tr.cousNm }}</td>
                    <td>{{ tr.cousWkdNm }}</td>
                    <td>{{ tr.cousTime }}</td>
                    <td>{{ tr.cousTime ? timeFormat(tr.cousTime) : "" }}</td>
                    <td>{{ tr.totalFixnuCunt }}</td>
                    <td>{{ tr.cousCage ? insertComma(parseInt(tr.cousCage)) : "" }}</td>
                </tr>
            </tbody>
        </table>
        <div class="btns-area text-center mt-40">
            <button @click="router.go(-1)" class="btn btn-white mr-12">목록</button>
            <button
                class="btn btn-green"
                @click="
                    async () => {
                        if (
                            !(await alert.confirm(
                                '온라인 수강신청은 스포츠클럽 홈페이지에서 신청이 가능합니다.\n이동하시겠습니까?'
                            ))
                        )
                            return;

                        $window.location.href = goUrl + data?.rpsUrl;
                    }
                "
            >
                온라인수강신청
            </button>
        </div>
    </div>
</template>

