                            background: #fff;
                        }
                    }
                }
            }
        }
        &-jongmok {
            margin-top: 80px;
            margin-bottom: 60px;
            li {
                padding: 10px 40px;
                border-radius: 28px;
                color: #767676;
                background: #f9f9f9;
                @include font-style(16);
                margin-right: 12px;
                &.is-active {
                    border: solid 1px #0fae6f;
